.wrapper{display: flex; flex-direction: column; height: 100vh; margin-top: 20px;}
.flex {margin-top: 20px;flex: 1;}
.recenter {text-align: center;}

/* Styling the icon box */
.icon-box {
  display: inline-block;
  margin: 0 16px;
}

/* Smooth transition for the theme */
html[data-theme="dark"] .icon-box {
  background-color: #10161f;
}

html[data-theme="light"] .icon-box {
  background-color: #fff;
}

/* Icon color changes for dark mode */
html[data-theme="dark"] .icon i {
  color: #f0f0f0; /* Light color for icons in dark mode */
}

html[data-theme="light"] .icon i {
  color: #333; /* Dark color for icons in light mode */
}

/* Hover effect on icon box */
.icon-box:hover {
  background-color: #007bff;
  color: #fff;
  // margin-top: 20px;
}
/* The button itself */
.to-top {
  visibility: hidden;
  position: fixed;
  right: 20px;
  bottom: 60px;
  background-color: #007bff;
  color: white;
  border: none;
  width: 60px;  /* Set the width */
  height: 60px; /* Set the height */
  cursor: pointer;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  display: flex;                    /* Use flexbox to center the content */
  justify-content: center;          /* Center horizontally */
  align-items: center;              /* Center vertically */
  font-size: 28px;                  /* Set the font size for the icon */
  border-radius: 50%;               /* Makes the button round */
  transition: all 0.3s ease;        /* Smooth transition for hover effect */
  z-index: 9999;                    /* Ensure the button stays on top */
}

/* When hovering over the button */
.to-top:hover {
  background-color: #0056b3;  /* Change button color on hover */
}

/* The icon itself */
.to-top i {
  margin: 0;                /* No margin around the icon */
  font-size: 28px;          /* Icon size adjusted for the button */
  line-height: 0;           /* Prevent any space that might affect the alignment */
  display: block;           /* Make sure the icon is a block level element */
}
